.content__body {
    margin-top: 163px;
}
@media (max-width: 767px) {
    .content__body {
        margin-top: 33px;
    }
}

.content__body h1,
h2 {
    font-family: Roboto;
    font-style: normal;
    font-weight: 700;
    font-size: 70px;
    line-height: 66px;
}
@media (max-width: 767px) {
    .content__body h1,
    h2 {
        font-size: 32px;
        line-height: 100%;
    }
}

.content__body h1 {
    color: #121212;
    margin-bottom: 9px;
}

.content__body h2 {
    color: #0ec261;
}

.content__body p {
    font-family: Roboto;
    font-weight: 300;
    font-size: 26px;
    line-height: 30px;
    margin: 25px 0 55px 0;
    color: #999999;
}
@media (max-width: 767px) {
    .content__body p {
        font-size: 18px;
        line-height: 21px;
        margin: 16px 0 32px 0;
    }
}


.content__body a {
    display: block;
    width: 250px;
    height: 48px;
    background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
    background-blend-mode: darken;
    border-radius: 8px;
    font-family: Roboto;
    font-weight: 400;
    font-style: normal;
    color: #ffffff;
    font-size: 18px;
    line-height: 48px;
    text-align: center;
}
@media (max-width: 767px) {
    .content__body a {
        position: absolute;
        width: 100%;
        border-radius: 0px;
        margin-left: -16px;
    }
}